/* Light-Themed Color Palette for NutriTrack App */
/* Light Mode Only (Fresh, Clean, Inviting) */
:root {
  --primary-mint: #C9EEC7; /* Mint Green for primary elements */
  --primary-sage: #96B486; /* Sage Green for primary elements */
  --secondary-light-beige: #F4EDE7; /* Light Beige for secondary elements */
  --secondary-warm-taupe: #D9CAB7; /* Warm Taupe for secondary elements */
  --background-soft-cream: #FAF8F4; /* Soft Cream for backgrounds */
  --accent-coral: #FA9B62; /* Coral Orange for accents */
  --accent-red-orange: #CA3E47; /* Red-Orange for accents */
  --text-dark: #2D3748; /* Dark gray for text */
  --text-secondary: #718096; /* Medium gray for secondary text */
  --shadow-light: rgba(150, 180, 134, 0.1); /* Soft shadow with sage tint */
  --shadow-medium: rgba(150, 180, 134, 0.15); /* Medium shadow */
  --border-radius-small: 8px;
  --border-radius-medium: 12px;
  --border-radius-large: 16px;
}

/* Universal Variables */
:root {
  --primary: var(--primary-sage);
  --primary-light: var(--primary-mint);
  --secondary: var(--secondary-light-beige);
  --secondary-dark: var(--secondary-warm-taupe);
  --background: var(--background-soft-cream);
  --accent: var(--accent-coral);
  --accent-dark: var(--accent-red-orange);
  --text: var(--text-dark);
  --text-secondary: var(--text-secondary);
  --shadow: var(--shadow-light);
  --shadow-hover: var(--shadow-medium);
}
