/* Modern Dark-Themed Landing Page for NutriTrack */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  font-family: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #0f0f23 0%, #1a1a2e 50%, #16213e 100%);
  min-height: 100vh;
  color: #ffffff;
  line-height: 1.6;
  overflow-x: hidden;
}

/* Navigation */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(15, 15, 35, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  z-index: 1000;
  padding: 1rem 0;
}

.nav-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.8rem;
  font-weight: 700;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  cursor: pointer;
}

.loginButton {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.3);
  color: #3b82f6;
  padding: 0.75rem 1.5rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  font-size: 0.9rem;
}

.loginButton:hover {
  background: rgba(59, 130, 246, 0.2);
  border-color: #3b82f6;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
}

/* Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 120px 2rem 80px;
  max-width: 1200px;
  margin: 0 auto;
  gap: 4rem;
}

.heroContent {
  flex: 1;
  max-width: 600px;
}

.heroTitle {
  font-size: 4rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  background: linear-gradient(135deg, #ffffff 0%, #e2e8f0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.highlight {
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.heroSubtitle {
  font-size: 1.25rem;
  color: #cbd5e1;
  margin-bottom: 2.5rem;
  line-height: 1.7;
  max-width: 500px;
}

.heroButtons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.ctaButton {
  padding: 1rem 2rem;
  border-radius: 50px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  text-decoration: none;
  display: inline-block;
}

.ctaButton.primary {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  color: white;
  box-shadow: 0 4px 20px rgba(59, 130, 246, 0.4);
}

.ctaButton.secondary {
  background: transparent;
  color: #3b82f6;
  border: 2px solid #3b82f6;
}

.ctaButton.primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(59, 130, 246, 0.5);
}

.ctaButton.secondary:hover {
  background: rgba(59, 130, 246, 0.1);
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(59, 130, 246, 0.3);
}

.heroVisual {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.phoneMockup {
  width: 300px;
  height: 600px;
  background: linear-gradient(145deg, #1e293b, #334155);
  border-radius: 40px;
  position: relative;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 12px solid #0f172a;
}

.phoneMockup::before {
  content: '';
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 150px;
  height: 25px;
  background: #0f172a;
  border-radius: 15px;
}

.screen {
  position: absolute;
  top: 60px;
  left: 12px;
  right: 12px;
  bottom: 12px;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
}

.sectionTitle {
  font-size: 2.5rem;
  color: #ffffff;
  text-align: center;
  margin-bottom: 60px;
  font-weight: 800;
  animation: fadeInUp 0.8s ease-out;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.features {
  padding: 80px 20px;
  background-color: #ffffff; /* White background */
  position: relative;
  z-index: 1;
}

.features .sectionTitle {
  color: black;
  text-shadow: none;
}

.featuresGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 50px;
  max-width: 1400px;
  margin: 0 auto;
}

.featureItem {
  background: white;
  padding: 40px 30px;
  border-radius: 25px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: all 0.4s ease;
  border: 1px solid #e8f5e9;
  position: relative;
  overflow: hidden;
}

.featureItem:hover {
  transform: translateY(-15px) scale(1.02) rotateX(5deg) rotateY(5deg);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
  perspective: 1000px;
}

.featureItem h3 {
  color: #000000; /* Black font for visibility on white background */
  margin-bottom: 20px;
  font-size: 1.4rem;
  font-weight: 700;
}

.featureItem p {
  color: #000000; /* Black font for readability */
  line-height: 1.7;
  font-size: 1.1rem;
}

.help {
  padding: 80px 20px;
  background: white;
  position: relative;
  z-index: 1;
  text-align: center;
}

.helpText {
  font-size: 1.4rem;
  color: black;
  line-height: 1.8;
  max-width: 1000px;
  margin: 0 auto;
  animation: fadeInUp 1s ease-out;
  font-weight: 500;
}

.steps {
  padding: 60px 20px;
  background: white;
}

.stepsSubtitle {
  font-size: 1.2rem;
  color: black;
  text-align: center;
  margin-bottom: 50px;
  animation: fadeInUp 0.9s ease-out;
}

.stepsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.stepCard {
  background: white;
  padding: 35px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid #e8f5e9;
}

.stepCard:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
}

.stepNumber {
  font-size: 2.5rem;
  color: #387c53;
  font-weight: bold;
  margin-bottom: 20px;
  animation: bounceIn 1s ease-out;
}

.testimonials {
  padding: 60px 20px;
  background: white;
}

.testimonials .sectionTitle {
  color: black;
  text-shadow: none;
}

.testimonialsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.testimonialCard {
  background: white;
  padding: 35px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid #e8f5e9;
}

.testimonialCard:hover {
  transform: translateY(-8px) scale(1.02) rotateY(10deg);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
}

.testimonialCard p {
  color: black;
  font-style: italic;
  margin-bottom: 15px;
  line-height: 1.6;
}

.testimonialCard cite {
  color: #387c53;
  font-weight: bold;
}

.cta {
  padding: 80px 20px;
  background: linear-gradient(135deg, #387c53 0%, #2e5f42 100%);
  color: #ffffff;
  text-align: center;
  border-radius: 30px 30px 0 0;
  margin-top: 60px;
}

.cta h2 {
  margin-bottom: 20px;
  font-size: 2.5rem;
}

.cta p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  opacity: 0.9;
}

.ctaButtonSecondary {
  background-color: #ffffff;
  color: #387c53;
  border: none;
  padding: 18px 35px;
  font-size: 1.2rem;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255,255,255,0.3);
}

.ctaButtonSecondary:hover {
  background-color: #f0f0f0;
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(255,255,255,0.4);
}

.footer {
  background-color: #387c53;
  color: #ffffff;
  padding: 50px 20px 30px;
  margin-top: 60px;
}

.footerContent {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.footerSection {
  text-align: center;
}

.footerSection h3 {
  margin-bottom: 20px;
  color: #ffffff;
}

.footerLinks {
  list-style: none;
  padding: 0;
}

.footerLink {
  color: #ffffff;
  text-decoration: none;
  display: block;
  margin-bottom: 12px;
  transition: all 0.3s ease;
}

.footerLink:hover {
  color: #e8f5e9;
  transform: translateX(5px);
}

.socialIcons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.socialIcon {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.socialIcon:hover {
  color: #e8f5e9;
  transform: scale(1.2);
}

.pricing {
  padding: 60px 20px;
  background: white;
}

.pricing .sectionTitle {
  color: black;
  text-shadow: none;
}

.pricingGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
  max-width: 1000px;
  margin: 0 auto;
}

.pricingCard {
  background: linear-gradient(135deg, #387c53 0%, #2e5f42 100%);
  color: #ffffff;
  padding: 40px 30px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
  border: 2px solid #e8f5e9;
  position: relative;
}

.pricingCard:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(0,0,0,0.15);
  border-color: #387c53;
}

.pricingCard.pro {
  background: linear-gradient(135deg, #4caf50 0%, #388e3c 100%);
  color: #ffffff;
  border-color: #4caf50;
  transform: scale(1.03);
}

.pricingCard.pro:hover {
  transform: translateY(-10px) scale(1.03);
  box-shadow: 0 15px 35px rgba(76, 175, 80, 0.3);
}

.pricingCard.premium {
  background: linear-gradient(135deg, #387c53 0%, #2e5f42 100%);
  color: #ffffff;
  border-color: #387c53;
  transform: scale(1.05);
}

.pricingCard.premium:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 15px 35px rgba(56, 124, 83, 0.3);
}

.pricingCard h3 {
  font-size: 1.8rem;
  margin-bottom: 15px;
  color: #ffffff;
}

.pricingCard.pro h3 {
  color: #ffffff;
}

.pricingCard.premium h3 {
  color: #ffffff;
}

.price {
  font-size: 2.5rem;
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 25px;
}

.pricingCard.premium .price {
  color: #ffffff;
}

.pricingCard ul {
  list-style: none;
  padding: 0;
  margin-bottom: 30px;
}

.pricingCard li {
  margin-bottom: 12px;
  color: #e8f5e9;
  font-size: 1rem;
}

.pricingCard.premium li {
  color: #e8f5e9;
}

.pricingButton {
  background-color: #387c53;
  color: #ffffff;
  border: none;
  padding: 15px 30px;
  font-size: 1.1rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  box-shadow: 0 4px 15px rgba(56, 124, 83, 0.3);
}

.pricingButton:hover {
  background-color: #2e5f42;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(56, 124, 83, 0.4);
}

.pricingButton.premium {
  background-color: #ffffff;
  color: #387c53;
  box-shadow: 0 4px 15px rgba(255,255,255,0.3);
}

.pricingButton.premium:hover {
  background-color: #f0f0f0;
  box-shadow: 0 6px 20px rgba(255,255,255,0.4);
}

.socialProof {
  padding: 60px 20px;
  background-color: #ffffff; /* White background */
}

.socialProof .sectionTitle {
  color: #000000; /* Black font for visibility on white background */
}

.socialProofGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.proofItem {
  background-color: #f8f9fa; /* Light gray background for contrast */
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  text-align: center;
  transition: all 0.3s ease;
  border: 1px solid #e8f5e9;
}

.proofItem:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.15);
  border-color: #387c53;
}

.statNumber {
  font-size: 2.5rem;
  color: #387c53;
  font-weight: bold;
  margin-bottom: 10px;
}

.proofItem p {
  color: #000000; /* Black text for readability */
}

.trustBadge {
  background-color: #387c53;
  color: #ffffff;
  padding: 10px 20px;
  border-radius: 25px;
  font-weight: bold;
  display: inline-block;
  margin-bottom: 10px;
}



/* Personalized Diet Plans Section */
.dietPlans {
  padding: 80px 20px;
  background: white;
  margin-bottom: 60px;
}

.dietPlansContent {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
}

.dietPlansText h3 {
  color: #2e7d32;
  font-size: 2rem;
  margin-bottom: 20px;
  font-weight: 600;
}

.dietPlansText p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #555;
  margin-bottom: 20px;
}

.dietPlansText ul {
  list-style: none;
  padding: 0;
  margin: 20px 0;
}

.dietPlansText li {
  margin-bottom: 12px;
  padding-left: 20px;
  position: relative;
  color: #333;
  font-size: 1rem;
  line-height: 1.5;
}

.dietPlansText li::before {
  content: "âœ“";
  position: absolute;
  left: 0;
  color: #2e7d32;
  font-weight: bold;
  font-size: 1.1rem;
}

.dietPlansText strong {
  color: #2e7d32;
  font-weight: 600;
}

.dietPlansVisual {
  background: white;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  border: 2px solid #e8f5e9;
}

.planExample h4 {
  color: #2e7d32;
  font-size: 1.5rem;
  margin-bottom: 25px;
  text-align: center;
  font-weight: 600;
}

.mealSlots {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.mealSlot {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 15px;
  background: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #2e7d32;
  transition: transform 0.2s ease;
}

.mealSlot:hover {
  transform: translateX(5px);
  background: #e8f5e9;
}

.mealTime {
  font-weight: 600;
  color: #2e7d32;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.mealName {
  color: #555;
  font-size: 1rem;
  font-weight: 500;
}

.footerBottom {
  text-align: center;
  margin-top: 40px;
  padding-top: 25px;
  border-top: 1px solid #4caf50;
}



/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes rotate3D {
  0% {
    transform: rotateX(0deg) rotateY(0deg);
  }
  25% {
    transform: rotateX(5deg) rotateY(5deg);
  }
  50% {
    transform: rotateX(0deg) rotateY(10deg);
  }
  75% {
    transform: rotateX(-5deg) rotateY(5deg);
  }
  100% {
    transform: rotateX(0deg) rotateY(0deg);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.3);
  }
  50% {
    box-shadow: 0 0 30px rgba(59, 130, 246, 0.6);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero {
    padding: 100px 20px 80px;
    margin-bottom: 60px;
  }

  .heroTitle {
    font-size: 2.8rem;
  }

  .heroSubtitle {
    font-size: 1.2rem;
  }

  .sectionTitle {
    font-size: 2.4rem;
  }

  .stepsGrid,
  .featuresGrid,
  .testimonialsGrid,
  .pricingGrid {
    grid-template-columns: 1fr;
  }

  .footerContent {
    grid-template-columns: 1fr;
  }

  .socialProofGrid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

@media (max-width: 480px) {
  .hero {
    padding: 80px 20px 60px;
    margin-bottom: 50px;
  }

  .heroTitle {
    font-size: 2.2rem;
  }

  .heroSubtitle {
    font-size: 1.1rem;
  }

  .sectionTitle {
    font-size: 2rem;
  }

  .ctaButton,
  .ctaButtonSecondary,
  .trialButton {
    padding: 16px 30px;
    font-size: 1.1rem;
  }

  .heroButtons {
    flex-direction: column;
    gap: 15px;
  }

  .proofItem {
    padding: 20px;
  }

  .statNumber {
    font-size: 2rem;
  }
}
